$mainBlue: #007DBF;
$text: #313131;
$borderGrey: #E2E3E2;

html {
    transition: all 500ms ease;
    background-color: #007DBF;
    //background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAICAYAAADED76LAAAAHElEQVQYlWO4ePHCfwYkgM5nwCeJwqeRJH3cAABipDcRG/i61wAAAABJRU5ErkJggg==')

    * {
        font-family: "Questrial", Georgia, serif;
        box-sizing: border-box;
 } }        //font-family: 'Lato', sans-serif

#outer-wrapper {
    display: table;
    position: absolute;
    height: 100%;
    width: 100%; }

#inner-wrapper {
    display: table-cell;
    vertical-align: middle; }

#logo {
    width: 100%;
    height: 104px;
    background: url('../img/logo--white.png') no-repeat center bottom / 64px;
    position: relative;
    z-index: 999;
    background-color: #37BDFF; }

#waves {
    overflow: hidden;
    position: relative;
    height: 200px;
