    //     content: ''
 }    //     background: linear-gradient(to bottom, rgba(#e8a, 1), rgba(#def, 0) 50%, rgba(white, .5))

.lang.en #content {
    #change-language {
        background-position: center right; }

    #main-text {
        transform: translate3d(-50%, 0px, 0px); } }

#content {
    max-width: 640px;
    margin: 0 auto;
    box-shadow: 0 2px 30px rgba(black, .2);
    border-radius: 9px 9px 7px 7px;
    background: #FFF;
    overflow: hidden;

    #change-language {
        background: url('../img/languages.png') no-repeat center left / 90px;
        display: block;
        width: 45px;
        height: 30px;
        transition: background-position 500ms ease;
        margin: 0 32px 20px 32px; }

    #main-text {
        width: 200%;
        transition: transform 500ms ease 0s;
        transform: translate3d(0%, 0px, 0px); }

    .text {
        line-height: 32px;
        color: rgb(34, 34, 34);
        padding: 0 32px 16px 32px;
        position: relative;
        z-index: 999;
        color: #444;
        display: block;
        width: 50%;
        float: left; }

    p {
        font-size: 20px;

        a {
            color: #000;

            &:hover {
                text-decoration: none; } } } }

.wave {
    opacity: .4;
    position: absolute;
    bottom: 25%;
    left: -50%;
    background: #0Af;
    width: 200%;
    transform-origin: 50% 48%;
    border-radius: 43%;
    transition: transform 500ms ease;
    animation: drift 2459*3ms infinite linear;

    &.w-three {
        animation: drift 3041*3ms infinite linear; }

    &.w-two {
        animation: drift 5021*3ms infinite linear;
        // opacity: .1
 } }        // background: yellow

@keyframes drift {
    from {
        transform: rotate(0deg); }

    from {
        transform: rotate(360deg); } }
